import { Component, Host, Input, OnDestroy, Optional } from '@angular/core';
import { NgControl } from '@angular/forms';
import { FormComponent } from '@sofico-framework/ui-kit/components/form';
import { InputNumberBaseDirective } from '@sofico-framework/ui-kit/components/number-base';
import {
  OnSofFocus,
  SOF_FOCUS_COMPONENT
} from '@sofico-framework/ui-kit/directives/focus';

@Component({
  selector: 'sof-input-currency',
  template: `
    <div class="input-group">
      <div class="input-group-prepend">
        <span class="input-group-text">{{
          currencyCode | sofCurrencySymbol
        }}</span>
      </div>
      <input
        #inputElement
        type="text"
        inputmode="decimal"
        [attr.id]="labelForId"
        [value]="internalValue"
        class="form-control"
        [class.is-invalid]="
          invalid ||
          (ngControl?.invalid && (ngControl?.touched || form?.submitted))
        "
        [placeholder]="placeholder"
        [disabled]="isDisabled"
        (input)="onChange($event.target?.value)"
        (blur)="onTouch()"
      />
    </div>
  `,
  providers: [
    { provide: SOF_FOCUS_COMPONENT, useExisting: InputCurrencyComponent }
  ]
})
export class InputCurrencyComponent
  extends InputNumberBaseDirective
  implements OnDestroy, OnSofFocus {
  @Input() currencyCode: string;

  constructor(
    @Optional() public form: FormComponent,
    @Optional() @Host() public ngControl: NgControl
  ) {
    super();
    if (ngControl) {
      ngControl.valueAccessor = this;
    }
  }

  sofFocus(): void {
    this.inputElement.nativeElement.focus();
  }

  ngOnDestroy(): void {
    if (this.ngControl?.valueAccessor) {
      // Every time a control is re-created the previous writeValue reference(s) is not cleaned up.
      // So, over time, a lot of these references can be built up. This memory leak is a bug in Angular's implementation of ControlValueAccessor.
      // We hide that problem by assigning an empty function to writeValue every time we destroy the control.
      // An detailed explanation of the problem can be found here: https://github.com/angular/angular/pull/29335
      // The bug issue for it: https://github.com/angular/angular/issues/20007
      this.ngControl.valueAccessor.writeValue = () => {};
    }
  }
}
